<template>
  <div class="container">
    <h1>Android性能曲线</h1>
    <ve-line :data="chartData" :settings="chartSettings"></ve-line>
    <span class="badge badge-pill badge-light">Native_AVG:{{chartData.Native_AVG}}MB</span>
    <span class="badge badge-pill badge-light">Dalvik_AVG:{{chartData.Dalvik_AVG}}MB</span>
    <span class="badge badge-pill badge-light">Cpu_AVG:{{chartData.cpu_avg}}</span>


  </div>
</template>

<script>
  module.exports = {
    data(){
      return{
        chartData:{}
      }

    },
    methods:{
      A: function() {
        setInterval(this.getdata, 500)
    },

      getdata:function(){
        this.$http.get('static/data.json').
      then((response) => {
        response = response.body;
        console.log('数据加载成功');
        this.chartData = response;
        // this.$set(this, chartData, response);
        console.log(this.chartData);
        console.log('数据加载成功');

      });
    }

    },

    
    created: function () {
      var self = this;
      // this.chartData = {'columns': ['time', 'cpu', 'Native Heap', 'Dalvik Heap'], 'rows': [{'time': '04-11 10:43:56', 'cpu': 0.0, 'Native Heap': 53.66, 'Dalvik Heap': 68.41}, {'time': '04-11 10:43:59', 'cpu': 0.0, 'Native Heap': 53.66, 'Dalvik Heap': 68.41}, {'time': '04-11 10:44:01', 'cpu': 0.0, 'Native Heap': 53.66, 'Dalvik Heap': 68.41}, {'time': '04-11 10:44:04', 'cpu': 0.0, 'Native Heap': 53.66, 'Dalvik Heap': 68.41}, {'time': '04-11 10:44:07', 'cpu': 0.0, 'Native Heap': 53.66, 'Dalvik Heap': 68.41}]}

      setInterval(this.getdata,1000)

      // this.$http.get('static/data.json').
      // then((response) => {
      //   response = response.body;
      //   console.log('数据加载成功');
      //   this.chartData = response;
      //   // this.$set(this, chartData, response);
      //   console.log(this.chartData);
      //   console.log('数据加载成功');

      // })

      this.chartSettings = {
        axisSite: {right: ['cpu']},
        max: [0, 1],
        yAxisType: ['normal', 'percent'],
        yAxisName: ['内存（MB)', 'cpu']
      }
    },
    computed: {
    fullName () {
      return this.chartData
    }
  },


  }

</script>

<style>


</style>



